/*Footer Styles */
.footer-container {
  padding-top: 80px;
  background-color: rgb(255, 255, 255);
  border-top-color: rgb(128, 10, 10);
  border-top-width: 2px;
  border-top-style: solid;
}
.footer-container h1 {
  margin-bottom: 50px;
  color: rgb(42, 42, 42);
  text-decoration: underline;
  text-underline-offset: 20px;
  text-decoration-color: red;

}
.footer {
  display: grid;
  justify-content: center;
  margin-bottom: 40px;
}
.email-container, .phone-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  font-size: 20px;
}
.footer img {
  height: 30px;
  margin-bottom: 10px;
}
.update-info {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
  background-color: rgb(230, 230, 230);
}
.update-info p {
  text-shadow: none;
  color: rgb(131, 131, 131);
}