/*Home Page */
.home-container-fade {
  background-image: url("backgrounds/dummy.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
  position: relative;
  z-index:2;
}
.home-container {
  position: absolute;
  display: grid;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.intro-container{
  padding-top: 6vh;
  height: 94vh;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.intro-container h1 {
  color: rgb(233, 239, 255);
  font-size: 50px;
  font-weight: 800;
  min-width: 575px;
  padding: 20px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  text-decoration: underline;
  text-decoration-color: rgba(183, 185, 226, .5);
  text-underline-offset: 8px;
}
.intro-container p {
  margin-top: 60px;
  min-width: 600px;
  width: 50%;
  font-size: 20px;
  font-weight: 800;
  padding: 20px;
  text-align: center;
  color: rgb(144, 186, 245);
  background-color: rgba(0, 0, 0, 0.5);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.intro-container button {
  margin-top: 100px;
  font-size: 20px;
  padding: 20px;
  font-weight: 800;
  color: white;
  border: none;
  border-radius: 50px;
  background-color: rgba(50, 53, 198, .9);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  transition: background-color .3s ease;
}
.intro-container button:hover {
  background-color: rgb(74, 76, 242);
}

