
.quick-links {
  display: grid;
  justify-content: center;
  align-items: center;
  height: 30vh;
  background-color: rgb(23, 17, 46);
  padding: 30px;
}

.quick-links h1 {
  color: white;
  font-size: 45px;
  text-shadow: 2px 2px 4px rgb(18, 103, 195);
}

.link {
  display: flex;
  align-items: center;
  justify-content: space-between;  
  padding-bottom: 40px;
  font-size: 20px;
  color: black;
}
.github {
  color: black;
  justify-self: center;
  margin-right: 30px;
  background: rgb(115, 134, 233);
  width: 100%;
  padding: 20px;
  border: 1px solid black;
  border-radius: 40px;
}
.linkedin {
  color: black;
  font-weight: 400;
  justify-self: center;
  margin-left: 30px;
  background-color: rgb(222, 222, 222); 
  width: 100%;
  padding: 20px;
  border: 1px solid black;
  border-radius: 40px;
}