/*Navbar Styles*/

.navbarInitial {
  height: 6vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  border-bottom-width: 1px;
  border-bottom-style: solid;

  z-index: 100;

  background-color: transparent;
  border-bottom-color: transparent;
  transition: background-color .1s ease;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.navbarScrolled {
  height: 6vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  border-bottom-width: 1px;
  border-bottom-style: solid;

  z-index: 100;

  background-color: rgb(255, 255, 255);
  border-bottom-color: rgb(105, 55, 55);
  transition: background-color .1s ease;
}
.navbarInitial h1 {
  cursor: pointer;
  margin-left: 100px;
  font-size: 20px;
  color: rgb(210, 207, 207);
  min-width: 200px;
  transition: color .3s ease;
}
.navbarScrolled h1 {
  cursor: pointer;
  margin-left: 100px;
  font-size: 20px;
  color: rgb(41, 41, 41);
  min-width: 200px;
  transition: color .3s ease;
}
.navbarInitial:hover h1 {
  color: rgb(255, 255, 255);
}
.navbarScrolled h1:hover {
  color: rgb(255, 85, 85);
}

.navbarScrolled .navbar-links {
  color: black;
}

.links-Scrolled, .links-Initial {
  display: flex;
  align-items: center;  
  padding: 2px 5px;
  margin: 0px 80px;
}
.links-Scrolled a, .links-Initial a{
  margin: 10px;
  text-shadow: none;
}
.links-Scrolled a{
  transition: color .3 ease;
  color: black;
}
