/*Projects Styles*/

.projects-page {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-image: url("backgrounds/dummy.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.projects-page h1{
  margin-top: 80px;
  color: rgb(255, 255, 255);
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.projects-container {
  max-width: 100%;
  max-height: 100%;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 20px;
  margin-top: 30px;
  padding: 20px;
  background-color: transparent;
  width: 50%;
  height: 100%;
}

.project-container:hover {
  opacity: 0;
}

.project-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: transparent;
  margin-bottom: 0;
  margin-top: 0;
  width: 100%;
  height: 100%;
}

.project-item img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.project-item p {
  color: white;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  border-radius: 30px;
  padding: 8px 15px;
  font-size: 25px;
}

.project {
  display: grid;
  align-items: center;
  width: 250px;
  height: 250px;
  background-color: rgba(0,0,0,.9);
  margin: 20px 20px 20px 20px;
  padding: 0px;
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  transition: box-shadow 0.3s ease;
}
.project:hover {
  box-shadow: 0 0 20px rgba(255, 252, 252, 0.935);
}
.project:hover + .project-item p{
  background-color: white;
}

.projects-container a h2 {
  color: transparent;
  background-color: transparent;
  color: rgb(246, 77, 77);
}